import { Col, Row, Form } from "antd";
import { useDispatch, useSelector } from "react-redux";
import React, { useEffect, useRef, useState, useMemo } from "react";
import {
  resetUserRegistration,
  userRegistrationInProgress,
} from "../../../redux/auth/AuthActions";
import FormInput from "../../components/Inputs/FormInput";
import PasswordStrengthBar from "react-password-strength-bar";
import { useNavigate } from "react-router-dom";
import CountrySelector from "../../components/Selectors/CountrySelector";
import FormTextArea from "../../components/Inputs/FormTextArea";
import CarouselTab from "../../components/Tabs/CarouselTab";
import Button from "../../components/Buttons/Button";
import UserTypesSelector from "../../components/Selectors/UserTypesSelector";
import { getFormHasError, uniqueID } from "../../../utilz/utilz";
import WarningModal from "../../components/Modals/WarningModal";
import { AUTH_ROUTES } from "../../../contants/RoutesEnum";
import { useLocation } from "react-router-dom";
import { useTranslation } from 'react-i18next';
import { routeFileSelector } from '../../../utilz/i18n';

const RegistrationForm = () => {
  const dispatch = useDispatch();
  let carouselRef = useRef({});
  const navigate = useNavigate();
  const { userRegistration } = useSelector((state) => state.auth);
  const [form] = Form.useForm();
  const [sectionOneForm] = Form.useForm();
  const [sectionTwoForm] = Form.useForm();
  const [sectionThreeForm] = Form.useForm();
  const [currentSlider, setCurrentSlider] = useState(0);
  const [sectionOneStatus, setSectionOneStatus] = useState(false);
  const [sectionTwoStatus, setSectionTwoStatus] = useState(false);
  const actionsButtonDisabled =
    sectionOneStatus && sectionTwoStatus ? false : currentSlider !== 2;
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, 'translation']);
  const onSubmit = async () => {
    const formOneStatus = await getFormHasError(sectionOneForm);
    const formTwoStatus = await getFormHasError(sectionTwoForm);
    const formThreeStatus = await getFormHasError(sectionThreeForm);
    if (!formOneStatus) {
      carouselRef.current.goTo(0);
      setCurrentSlider(0);
    } else if (!formTwoStatus) {
      carouselRef.current.goTo(1);
      setCurrentSlider(1);
    } else if (!formThreeStatus) {
      carouselRef.current.goTo(2);
      setCurrentSlider(2);
    } else {
      let payload = {
        ...sectionOneForm.getFieldsValue(),
        ...sectionTwoForm.getFieldsValue(),
        ...sectionThreeForm.getFieldsValue(),
      };
      dispatch(userRegistrationInProgress(payload));
    }
  };
  useEffect(() => {
    return () => {
      dispatch(resetUserRegistration());
    };
  }, []);
  const onNextButtonClick = async (currentSlide) => {
    if (currentSlide === 1) {
      let status = await getFormHasError(sectionOneForm);
      if (status) {
        setSectionOneStatus(true);
        return true;
      }
      sectionOneForm.submit();
    } else if (currentSlide === 2) {
      let status = await getFormHasError(sectionTwoForm);
      if (status) {
        setSectionTwoStatus(true);
        return true;
      }
      sectionTwoForm.submit();
    } else if (currentSlide === 3) {
      let status = await getFormHasError(sectionThreeForm);
      if (status) {
        return true;
      }
      sectionThreeForm.submit();
    }
    return false;
  };

  return (
    <div className="flex-1 py-20">
      <Form onFinish={onSubmit} form={form}>
        <CarouselTab
          id={uniqueID()}
          activeKey={currentSlider}
          ref={carouselRef}
          handleSlideChange={setCurrentSlider}
          items={[
            {
              content: <FormSectionOne form={sectionOneForm} />,
            },
            {
              content: <FormSectionTwo form={sectionTwoForm} />,
            },
            { content: <FormSectionThree form={sectionThreeForm} /> },
          ]}
          onNext={onNextButtonClick}
        />

        <Row id={uniqueID()} justify="center" className="mt-20">
          <Col id={uniqueID()} sm={20} lg={10}>
            <Row
              id={uniqueID()}
              className="flex-direction-column"
              align={"middle"}
            >
              <Col className="w-100 mt-20">
                <Button
                  disabled={actionsButtonDisabled}
                  className="w-100"
                  buttonType="submit"
                  loading={userRegistration.progressing}
                >
                  {t("translation:signUp.form.submit")}
                </Button>
              </Col>
              <Col className="w-100 mt-20 text-center">
                <a
                  id={uniqueID()}
                  className={`register-reset-text f-16 mt-2 `}
                  onClick={() => form.resetFields()}
                >
                  {t('translation:default.fields.reset')}
                </a>
              </Col>
            </Row>
          </Col>
        </Row>
      </Form>
      <WarningModal
        open={userRegistration.success}
        description={
          t("translation:signUp.form.warning.desc")
        }
        okText={t("translation:signUp.form.warning.okText")}
        onOk={() => navigate(AUTH_ROUTES.LOGIN)}
        onClose={() => navigate(AUTH_ROUTES.LOGIN)}
      />
    </div>
  );
};

export default RegistrationForm;

const FormSectionOne = ({ form }) => {
  const passwordValue = Form.useWatch("password", form);

  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, 'translation']);
  return (
    <Form form={form}>
      <Row className={"flex-1 px-2"} gutter={30}>
        <Col lg={12} sm={24}>
          <Row>
            <Col sm={24}>
              <FormInput
                margin={"mb-15"}
                editMode={true}
                label={t('translation:default.fields.email')}
                name={"email"}
                rules={[
                  {
                    required: true,
                    message: t("translation:signUp.form.email.enter"),
                  },
                  { type: "email", message: t('translation:default.notification.email')},
                ]}
                maxLength={"150"}
              />
            </Col>
            <Col sm={24}>
              <UserTypesSelector
                margin={"mb-15"}
                editMode={true}
                label={t("translation:default.fields.userType.label")}
                name={"selectedUserType"}
                rules={[
                  {
                    required: true,
                    message:  t("translation:default.fields.userType.enter"),
                  },
                ]}
              />
            </Col>
          </Row>
        </Col>
        <Col lg={12} sm={24}>
          <Row>
            <Col sm={24} className={"password-length-bar-container"}>
              <FormInput
                inputType="password"
                margin={"mb-15"}
                Password
                editMode={true}
                label={t("translation:signUp.form.password.label")}
                name={"password"}
                rules={[{ required: true, message: t("translation:signUp.form.password.enter") }]}
              />
              <PasswordStrengthBar
                className="password-length-bar"
                password={passwordValue}
                shortScoreWord={"short"}
              />
            </Col>
            <Col sm={24}>
              <FormInput
                inputType="password"
                margin={"mb-15"}
                Password
                editMode={true}
                label={t("translation:signUp.form.confirmPassword.label")}
                name={"confirmPassword"}
                rules={[
                  {
                    required: true,
                    message: t("translation:signUp.form.confirmPassword.enter"),
                  },
                  {
                    validator(_, value) {
                      if (value !== passwordValue) {
                        return Promise.reject(
                          t("translation:signUp.confirmPassword")
                        );
                      }
                      return Promise.resolve();
                    },
                  },
                ]}
              />
            </Col>
          </Row>
        </Col>
      </Row>
    </Form>
  );
};

