import { Col, Row } from "antd";
import React, { useEffect, useMemo } from "react";
import Paper from "../../components/Paper/Paper";
import RegistrationForm from "../../screens/AuthScreens/RegistrationForm";
import WithImageWraper from "../../components/Common/WithImageWraper";
import AuthLeftPanel from "./components/AuthLeftPanel";
import AuthRightPanel from "./components/AuthRightPanel";
import { AUTH_ROUTES } from "../../../contants/RoutesEnum";
import { useDispatch } from "react-redux";
import { setAppLoader } from "../../../redux/common/CommonActions";
import { useLocation } from "react-router-dom";
import { useTranslation } from 'react-i18next';
import { routeFileSelector } from '../../../utilz/i18n';

const RegistrationPage = () => {
  const dispatch = useDispatch();
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, 'translation']);

  useEffect(() => {
    dispatch(setAppLoader(false));
  }, []);
  
  return (
    <WithImageWraper>
      <div className="login-page-container">
        <div className="w-80 vh-90 overflow-hidden br-4">
          <Paper className={"zIndex-9999 h-100  br-4"}>
            <Row className="h-100">
              <Col lg={6}>
                <AuthLeftPanel
                  link={AUTH_ROUTES.LOGIN}
                  linkText={t('translation:signUp.authLeft.link')}
                  title={t('translation:signUp.authLeft.title')}
                />
              </Col>
              <Col lg={18} className={"h-100 background-white py-20"}>
                <AuthRightPanel
                  title={t("translation:signUp.authRight.title")}
                  description={t("translation:signUp.authRight.desc")}
                >
                  <RegistrationForm />
                </AuthRightPanel>
              </Col>
            </Row>
          </Paper>
        </div>
      </div>
    </WithImageWraper>
  );
};

export default RegistrationPage;
