import { Carousel, Col, Row } from "antd";
import React, { forwardRef, useEffect } from "react";
import { useState } from "react";
import { useRef } from "react";
import IconButton from "../Buttons/IconButton";

const CarouselTab = forwardRef(
  (
    { items, handleSlideChange = () => {}, onNext = false, activeKey = false },
    ref
  ) => {
    const [currentSlide, setcurrentSlide] = useState(0);
    let carouselRef = useRef({});

    if (ref) {
      carouselRef = ref;
    }

    const handleNavigation = (value) => {
      carouselRef.current.goTo(value);
    };

    const handlePrevious = () => {
      carouselRef.current.prev();
    };

    const handleNext = async () => {
      let status = true;
      if (onNext !== false) {
        status = await onNext(currentSlide + 1);
      }
      if (status) {
        carouselRef.current.next();
      }
    };

    useEffect(() => {
      handleSlideChange(currentSlide);
    }, [currentSlide, handleSlideChange]);

    useEffect(() => {
      if (activeKey !== false) {
        setcurrentSlide(activeKey);
      }
    }, [activeKey]);

    return (
      <div className="custom-carouser-slider-container">
        <div>
          <Carousel
            afterChange={(key) => setcurrentSlide(key)}
            infinite={false}
            initialSlide={currentSlide}
            className=""
            ref={carouselRef}
            dots={false}
            arrows={false}
          >
            {items.map(({ content }, idx) => (
              <div key={idx} className="flex-1">
                {content}
              </div>
            ))}
          </Carousel>
        </div>
        <Row
          justify={"center"}
          align={"middle"}
          gutter={10}
          className={"mt-15"}
        >
          <Col className="lh-0">
            <IconButton
              onClick={handlePrevious}
              disabled={currentSlide === 0}
              className="action-buttons"
            >
              {"<"} Previous
            </IconButton>
          </Col>
          <Col>
            <ul className="dots-container mt-2">
              {items.map((row, idx) => (
                <li
                  onClick={() => handleNavigation(idx)}
                  className={`${currentSlide === idx ? "active" : ""}`}
                  key={idx}
                />
              ))}
            </ul>
          </Col>
          <Col className="lh-0">
            <IconButton
              onClick={handleNext}
              disabled={currentSlide === items.length - 1}
              className="action-buttons"
            >
              Next {">"}
            </IconButton>
          </Col>
        </Row>
      </div>
    );
  }
);

export default CarouselTab;
