import React, { useEffect, useState } from "react";
import { getUserTypesEffect } from "../../../redux/auth/AuthEffects";
import FormSelectBox from "../Inputs/FormSelectBox";
import i18n from 'i18next';

const UserTypesSelector = ({
  placeholder = i18n.t('translation:default.fields.userType.placeholder'),
  label = i18n.t('translation:default.fields.userType.label'),
  rules = [],
  name,
  editMode,
}) => {
  const [options, setOptions] = useState([]);
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    getStatusList();
  }, []);

  const getStatusList = async () => {
    try {
      setLoading(true);
      const { data } = await getUserTypesEffect();
      let options = data.map((row) => ({
        label: row.description,
        value: row.code,
      }));
      setOptions(options);
      setLoading(false);
    } catch (error) {
      console.log(error);
      setOptions([]);
      setLoading(false);
    }
  };

  return (
    <FormSelectBox
      loading={loading}
      options={options}
      placeholder={placeholder}
      label={label}
      margin={"mb-0"}
      rules={rules}
      name={name}
      editMode={editMode}
    />
  );
};

export default UserTypesSelector;
